<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>基础设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 部门设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            span="6"
          >
          <FormItem label="机构名称">
            <Select
              v-if="!isDisabled"
              v-model="searchData.orgId"
              clearable
              :transfer="transferValue"
              filterable
              placeholder="请选择机构名称"
            >
              <Option
                v-for="item in businessList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span: 6}"
          >
          <FormItem label="部门名称">
            <Input
              v-model="searchData.officeName"
              clearable
              placeholder="请填写部门名称"
            ></Input>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2, offset: 1 }"
          >
          <Button
            type="primary"
            @click="getBusinessData()"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="delData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(2)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/office/exportOfficeListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && !showTitle">请设置部门信息</span>
          <span v-else>部门信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <!--<Col span="11"></Col>-->
                <Col span="12">
                <FormItem
                  label="机构名称"
                  prop="orgId"
                >
                  <Select
                    v-if="!isDisabled"
                    v-model="form.orgId"
                    clearable
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择机构名称"
                  >
                    <Option
                      v-for="item in businessList"
                      :key="item.orgId"
                      :value="item.orgId"
                    >{{ item.orgName }}</Option>
                  </Select>
                  <Input
                    v-else
                    v-model.trim="form.orgName"
                    disabled
                    placeholder="请填写部门名称"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="部门名称"
                  prop="officeName"
                >
                  <Input
                    v-model.trim="form.officeName"
                    :disabled="isDisabled"
                    clearable
                    placeholder="请填写部门名称"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem label="备注">
                <Input
                  v-model.trim="form.officeRemark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name7"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="resetType('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>

    <!---->
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'BusinessManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      // 分页的总数
      total: 0,
      //  是否显示 修改 弹出框名称
      showTitle: false,
      // 部门下拉框
      businessList: [],
      // 当前选择的行数据
      currentRow: '',
      isDisabled: false,
      isSelected: false,
      transferValue: true,
      form: {
        orgId: '',
        // 部门
        officeName: '',
        officeRemark: ''
      },
      ruleValidateType: {
        orgId: [
          { type: 'number', required: true, message: '请填写机构名称', trigger: 'change' }
        ],
        officeName: [
          { required: true, message: '请选择部门名称', trigger: 'change' }
        ]
      },
      // 筛选内容
      searchData: {
        orgId: null,
        officeName: '',
        ascending: false,
        orderBy: 'officeId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      selTypeMod: false,
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '机构名称',
          key: 'orgName',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.orgName);
          }
        },
        {
          title: '部门名称',
          key: 'officeName'
        }
      ],
      data: [
      ]
    }
  },
  mounted() {
    this.getAllList()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    getAllList() {
      this.getOrganize()
      // 列表数据
      this.getBusinessData()
    },
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getAllList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getAllList()
    },
    // 删除数据
    delData() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/office/deleteOffice/' + that.currentRow.officeId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false
              that.$Message.success(res.msg);
              that.getBusinessData()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    },
    // 获取机构名称
    getOrganize() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.businessList = res.data.orgList
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getBusinessData() {
      const that = this;
      that.$set(that, 'data', []) // 需要做这个操作，以防空数据留至其他行
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/office/searchOffice/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const listData = res.data
          that.total = listData.totalCount
          that.data = listData.officeList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    // 新增
    createData() {
      // 是否显示 修改 弹出框名称
      this.showTitle = false;
      this.isDisabled = false;
      this.selTypeMod = true;
      this.clearData()
    },
    clearData() {
      this.form = {
        orgId: '',
        // 部门
        officeName: '',
        officeRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow;
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      const row = Object.assign({}, this.currentRow)
      // 显示弹出框名称
      this.clearData()
      this.showTitle = true;
      this.isDisabled = false;
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      this.showData(row)
    },
    showData(data) {
      this.form = data
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.selTypeMod = true
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 添加
          if (!that.showTitle) {
            this.commonUtil.ajaxPostUtil('/office/addOffice', form, (res) => {
              // 关闭数据加载
              this.$Spin.hide();
              if (res.code === 1) {
                this.$Message.success(res.msg);
                this.selTypeMod = false;
                this.getBusinessData()
              } else {
                this.$Message.error(res.msg);
              }
            }, (error) => {
              this.$Message.error('网络连接错误');
              // 关闭数据加载
              this.$Spin.hide();
            })
          } else {
            // 修改
            this.commonUtil.ajaxPutUtil('/office/updateOffice', form, (res) => {
              // 关闭数据加载
              this.$Spin.hide();
              if (res.code === 1) {
                this.selTypeMod = false;
                this.$Message.success(res.msg);
                this.getBusinessData()
              } else {
                this.$Message.error(res.msg);
              }
            }, (error) => {
              this.$Message.error('网络连接错误');
              // 关闭数据加载
              this.$Spin.hide();
            })
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearData();
      this.isDisabled = true;
      this.showData(data)
    }
  }
}
</script>

<style>
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.table {
  margin: 15px 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
</style>



